@import '../../assets/globals.scss';

.StoryShort{
    font-size: 1.3rem;
    margin-top: 0;
    color: $color-light-grey;
    text-align: left;
    margin: 0 0;

    @include respond(tab-land) {
        font-size: 1.7rem;
      }
  
      @include respond(tab-port){
        font-size: 1.9rem;
      }
  
      @include respond(phone){
        margin-top: 2.5rem;
        font-size: 2rem;
        margin-bottom: 2rem;
      }
  
      @include respond(s-phone){
        font-size: 2rem;
      }

}
