@import '../../assets/globals.scss';

.StoryButton{
    background: none;
	color: $color-gold;
	border: 0.2rem solid $color-gold;
	padding: 0.5rem;
	font: inherit;
	cursor: pointer;
	outline: inherit;
    text-align: center;
    margin-top: 2.5rem;
    font-size: 1.9rem;

    transition: all 0.2s ease-in;

    &:hover{
        background-color: $color-gold;
        color:white;
    }

    @include respond(tab-land) {
        font-size: 2.2rem;
      }
  
      @include respond(tab-port){
        font-size: 2.3rem;
      }
  
      @include respond(phone){
        font-size: 2.5rem;
      }
  
      @include respond(s-phone){
        font-size: 2.5rem;
      }

}