@import '../../assets/globals.scss';

.NavbarContainer {
  display: flex;
  flex-direction: row;
  margin-bottom: 3rem;
  justify-content: center;
  font-family:'Segoe UI';

  font-size: 1.3rem;

  @include respond(phone) {
    display: none;
  }

  & li {
    display: inline-block;
    margin: 0 5rem;
    transition: all .1s linear;
    
    &:hover{
      cursor: pointer;
      color: $color-gold;
    }

  }

}

.NavbarContainer__mobile{
  width: 100vw;
}

.menu--mobile {
  display: none;
  position: relative;
  z-index: 999999;
  height: 3.5rem;

  @include respond(phone) {
    display: block;
  }

  &__element {
    position: relative;
    background-color: $color-dark-grey;
    color: $color-gold;
    text-align: right;
    text-decoration: none;
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: 20px 20px;

    &:hover{
      cursor: pointer;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    padding-top: 4rem;
    list-style: none;
    overflow: hidden;
    clear: both;
    max-height: 0;
    transition: max-height 0.2s ease-out;
  }

  a {
    display: block;
    border-right: 1px solid #f4f4f4;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      background-color: #f4f4f4;
    }
  }
}

.menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 5px 20px;
  position: relative;
  user-select: none;
}

.menu-btn {
  display: none;

  &:checked ~ .menu--mobile__list {
    max-height: 240px;
  }

  &:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  &:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
  }
  &:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
  }

  &:checked ~ .menu-icon:not(.steps) .navicon:before,
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
}

.navicon {
  background: $color-gold;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;

  &:before {
    top: 5px;
  }

  &:after {
    top: -5px;
  }

  &:before,
  &:after {
    background: $color-gold;
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 100%;
  }
}

@media (min-width: 48em) {
  .menu--mobile__list li {
    float: right;
  }
  .menu--mobile__list li a {
    padding: 20px 30px;
  }
  .menu--mobile__list {
    clear: none;
    float: right;
    max-height: none;
  }
  .menu--mobile__list .menu-icon {
    display: none;
  }
}