@import './assets/globals.scss';


.LogoAndHeaderContainer {

  width:100%;
  height:95vh; 
  display:flex; 
  flex-direction:column; 
  justify-content: space-between;
  align-items:center;
  padding-top: 2rem;


  @include respond(big-desktop) {

  }

  @include respond(tab-land) {
 
  }

  @include respond(tab-port){
    height:90vh; 
  }

  @include respond(phone){
    padding-top: 6rem;
  }

  @include respond(s-phone){
   
  }

}

.imageContainer{
  flex-basis: 0.25;
  margin-bottom: 3rem;
}

.paragraphsContainer{

  width: 60% !important;
  margin: 0 auto;
  margin-top: 0;
  flex-basis: 0.45;

  @include respond(tab-land) {
      width: 70% !important;
  }

  @include respond(tab-port){
    width: 75% !important;
  }

  @include respond(phone){
    width: 80% !important;
  }

  @include respond(s-phone){
    width: 100% !important;
  }

}

.btnContainer{
  flex-basis: 0.05;
  margin-top: 6rem;
}

.Modal{
  width: 80%;
  margin: 0 auto;
  background-color: #ebecf0;
  margin-top: 2rem;
  padding: 2rem;
  overflow: scroll;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
}

.modalHeaderContainer{
  display: flex;
  flex-direction: row;
}

.contactMeContainer{
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  padding-left: 17vw;
  text-align: start!important;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-top: 5rem;


  @include respond(tab-port){
    padding-left: 10vw;
  }

  @include respond(phone){
    padding-left: 5vw;
  }

  @include respond(s-phone){
    padding-left: 5vw;
  }
}

.inputField{
  font-family: inherit;
  display: block;
  width: 50vw;

  @include respond(big-desktop) {
  }

  @include respond(tab-land) {
      width: 60vw;
  }

  @include respond(tab-port){
    width: 70vw;
  }

  @include respond(phone){
    width: 80vw;
  }

  @include respond(s-phone){
    width: 80vw;
  }
  
  

  &--email{
    margin-top: 1rem;
    height: 2.5rem;
    padding: 0.7rem 0.8rem;

  }

  &--message{
    margin-top: 2.5rem;
    height: 10rem;
    padding: 1rem 0.8rem;
  }

}

.errorLabel{
  display: block;
  text-align: start;
  margin-top: 0.4rem;
  color: $color-cherry;
  font-size: 1rem;

  @include respond(tab-land) {
      font-size: 1.4rem;
  }

  @include respond(tab-port){
    font-size: 1.6rem;
  }

  @include respond(phone){
    font-size: 1.8rem;
  }

  @include respond(s-phone){
    font-size: 1.8rem;
  }

}

.sentMessage{
  display: block;

  font-size: 1rem;

  @include respond(tab-land) {
      font-size: 1.4rem;
  }

  @include respond(tab-port){
    font-size: 1.6rem;
  }

  @include respond(phone){
    font-size: 1.8rem;
  }

  @include respond(s-phone){
    font-size: 1.8rem;
  }
}

.SubmitButton{
  padding: 0.7rem 6rem!important;
  margin-bottom: 10rem; //better fix?
}

.headerAndParagraphsContainer{

  flex-basis: 50%;
  margin-top: 4rem;
  text-align: center;

}

.firstStory{
  @include respond(phone){
    margin-top: 5rem!important;
  }
}