@import '../../assets/globals.scss';

.StoryImage{
    flex-basis: 35%;
    box-shadow: 2px 2px 22px 1px rgba(0,0,0,0.2);
    transition: all .5s ease-out;
    margin-top: 0;
    &:hover{
        flex-basis: 55%;
    }

    @include respond(tab-port){
        flex-basis: 45%;
    }

    @include respond(phone){
        width: 80vw;
        align-self: flex-start;
    }

}