@import '../../assets/globals.scss';


p{
    font-size: 1.5rem;
    color:$color-light-grey;
    margin-left: 10rem;
    margin-right: 10rem;

    @include respond(tab-land) {
      font-size: 1.9rem;
    }

    @include respond(tab-port){
      font-size: 2.1rem;
    }

    @include respond(phone){
      font-size: 2.2rem;
    }

    @include respond(s-phone){
      font-size: 2.2rem;
    }


}